import Toast from '../../../wxcomponents/dist/toast/toast';
export default {
	data() {
		return {
			password: '',
			username: ''
		}
	},
	methods: {
		// 登录
		loginByAccount() {
			this.$minApi.loginByAccount({
				password: this.password,
				username: this.username,
				userSchoolId: '1',
				userIdenty: '1'
			}).then(data => {
				Toast(data.msg);
				console.log(data)
				uni.setStorageSync('token', data.data.token);
				uni.switchTab({
					url: '../home/index/index'
				});
			})
		}
	}
}
